<template>
  <div class="container">
    <div class="wraper_name">
     <div class="back-btn">
      <backBtn @click="$router.go(-1)" />
    </div>    
    <form >
      <inputText :warna="warna" @data-input="handleDepan"/> 
      <inputText :warna="warna" idName="nama_heyoo_belakang" textLabel="Nama Belakang" @data-input="handleBelakang"/>


    </form>
    </div>
    <p class="danger-text">{{ pesan }}</p>
     <sendBtn :warna="warna.primary" v-if="active" @click="handleClick"/>
     <sendBtn :warna="warna.text" v-else/>
  </div>
</template>

<script>
import inputText from '../components/etc/inputText.vue'
/*{
idName,
textLabel,
warna,
placeholder
}*/
import backBtn from '../components/mats/backBtn.vue'
import sendBtn from '../components/mats/sendBtn.vue'

  export default{
    name: 'changeName',
    data() {
      return {
        namaDepan: '',
        namaBelakang: '',
        active: false,
        pesan: '',
        token: localStorage.getItem('xtoken')
      }
    },
    components: {
      inputText,
      backBtn,
      sendBtn
    },
    methods: {
      handleDepan(data) {
        this.namaDepan = data;
        this.validatornya();
        this.fullName =  this.namaDepan + ' ' + this.namaBelakang

      },
      handleBelakang(data) {
        this.namaBelakang = data;
        this.fullName = this.namaDepan + ' ' + this.namaBelakang

        this.validatornya();
      },
      validatornya() {
        if(this.namaDepan.length + this.namaBelakang.length >= 5 && this.namaDepan.length + this.namaBelakang.length <= 15){
          this.active = true;
          this.pesan = ''
        } else {
          this.active = false;
          this.pesan = 'Nama depan dan nama belakang minimal 5 huruf maximal 15 huruf'
        }
      },
      handleClick() {
        this.$swal.fire({
          icon: 'info',
          title: 'Yakin?',
          text: 'Nama anda akan di ganti, setelah ganti nama anda tidak akan bisa ganti nama selama 2 bulan',
          showCancelButton: true,
          cancelButtonText: 'Tidak'
        }).then(async r => {
          if(r.isConfirmed) {
            const res = await this.axios.put('/Authentification/changeName', {
                'new_name': this.fullName.trim()
            }, {
              headers: {
                authorization: `Bearer ${this.token}`
              }

            })

            alert(JSON.stringify(res));
          }
        })
      }
    },
    props: {
      warna: {
        type: Object,
        required: true
      }
    },
    created() {
      this.$emit('hilang-navbar', false)
    }
  }
</script>

<style scoped>
.container {
  position: relative;
  width: 100%;
  padding: 0.3rem 0.5rem;
}
.wraper_name {
  max-width: 800px;
  margin: 0 auto;
}
.danger-text {
  color: #a32222;
  font-size: 0.6rem;
}
</style>
