<template>
  <div class="user_chat">
    <HeaderChat :warna="warna" @hilang-navbar="$emit('hilang-navbar', false)" @tampil-navbar="$emit('hilang-navbar', true)"/>
    <bodyChat :warna="warna" :pesan="dataPesan"/>
    <inputChat :warna="warna" @send-pesan="handleSend"/>
  </div>
</template>

<script>
import HeaderChat from './HeaderChat.vue';
import bodyChat from './bodyChat.vue';
import inputChat from './inputChat.vue';

  export default{
    name: 'userChat',
    data() {
      return {
        dataPesan: {}
      }
    },
    components: {
      HeaderChat,
      bodyChat,
      inputChat
    },
    props: {
      warna: Object
    },
    methods: {
      handleSend(val){
        this.dataPesan = val;
      }
    }
  }
</script>

