<template>
  <div class="bagian_react">
    <div class="container">
      <div class="love" @click="addLove">
        <img src="../../assets/icon/ant-design_heart-filled.svg" alt="love">
        <span v-if="react > 0">{{ react }}</span>
        <span v-else></span>
      </div>
      <div class="comment" @click="addKomen(id)">
        <img src="../../assets/icon/akar-icons_comment.svg" alt="commen">
        <span v-if="comment > 0">{{ comment }}</span>
        <span v-else></span>
      </div>
      <div class="share">
        <img src="../../assets/icon/el_share-alt.svg" alt="share">
      </div>
    </div>
  </div>
   <transition name="fade-up">
 <div class="bagian_comment" v-if="komen">
      <bagianComment @buka-tab="hilangTab" :color="color"/>
    </div>
   </transition>
</template>

<script>
import bagianComment from './bagianComment.vue'

  export default{
    name: 'bagianReact',
    data() {
      return {
        love: 0,
        komen: false,
        token: localStorage.getItem('xtoken')
      }
    },
    props: {
      id: Number,
      color: Object,
      react: Number,
      comment: Number
    },
    methods: {
      async addLove() {
        try {
          const response = await this.axios.post('/Reaction', {
            'react_id': this.id
          }, {
            headers: {
              authorization: `Bearer ${this.token}`
            }
          })
          console.log(response)
        } catch(e) {
          alert(e)
        }
      },
      addKomen(id) {
        this.komen = true;
        console.log(id)
      },
      hilangTab(b) {
        this.komen = b;
      }
    },
    components: {
      bagianComment
    }
  }
</script>

