import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Friends from '../views/Friends.vue'
import Chat from '../views/Chat.vue'
import userChat from '../components/etc/userChat.vue'
import notifications from '../views/notifications.vue'
import Settings from '../views/Settings.vue'
import Login from '../views/Login.vue'
import register from '../views/register.vue'
import forget from '../views/forget.vue'
import accsetting from '../views/accsetting.vue'
import changeName from '../views/changeName.vue'
const changePassword = () => import('../views/changePassword.vue')
const changeEmail = () => import('../views/changeEmail.vue')
const addPost = () => import('../views/addPost.vue')
//const index = () => import('../views/H.vue')
const logout = () => import('../views/logout.vue')
const singlePost = () => import('../views/singlePost.vue')
const profile = () => import('../views/profile.vue')
const verify = () => import('../views/verify.vue')
const editProfile = () => import('../views/editProfile.vue');


const routes = [
  {
    path: '/u/:username/edit/:id',
    name: 'editProfile',
    component: editProfile
  },
  {
    path: '/Verifikasi/:key',
    name: 'verify',
    component: verify
  },
  {
    path: '/u/:username',
    name: 'Profile',
    component: profile
  },
  {
    path: '/loading',
    name: 'loading',
    component: () => import('../components/Loading.vue')
  },
  {
    path: '/detail-post/:id',
    name: 'DetailPost',
    component: singlePost
  },
  {
    path: '/',
    name: 'index',
    component: Home
  },
  {
    path: '/home',
    name: 'Home',
    component: Home
  },
  {
    path: '/friends',
    name: 'Friends',
    component: Friends
  },
  {
    path: '/chats',
    name: 'Chats',
    component: Chat
  },
  {
    path: '/chat/with/:id',
    name: 'Chating',
    component: userChat
  },
  {
    path: '/notification',
    name: 'Notifications',
    component: notifications
  },
  {
    path: '/settings',
    name: 'Settings',
    component: Settings
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/register',
    name: 'Register',
    component: register
  },
  {
    path: '/forgot',
    name: 'Forgot',
    component: forget
  },
  {
    path: '/account',
    name: 'Account',
    component: accsetting
  },
  {
    path: '/account/change-name',
    name: 'ChangeName',
    component: changeName
  },
  {
    path: '/account/change-password',
    name: 'ChangePassword',
    component: changePassword
  },
  {
    path: '/account/change-email',
    name: 'ChangeEmail',
    component: changeEmail
  },
  {
    path: '/add-post',
    name: 'AddPost',
    component: addPost
  },
  {
    path: '/logout',
    name: 'logout',
    component: logout
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
