<template>
  <div class="wraper_log" :style="{'--primary': color.primary, '--danger': color.hearth}">
    <div class="container_log">
      <div class="header_log">
        <h1 ref="title" :style="{color: color.text}"><span :style="{color: color.primary}">HeYoo</span> Story</h1>
        <p ref="desc" :style="{lineHeight: '140%', color: color.text, letterSpacing: '0.4px' }">Login ke <span :style="{color: color.primary}">HeYoo</span> untuk <b>berinteraksi</b> dengan teman baru, sekarang waktunya untuk memulai berbagi <b>pengetahuan</b> dan <b>cerita</b> dengan teman mu</p>
      </div>
      <form ref="form" class="body_log" @submit.prevent="onSubmit" :style="{'--primary': color.primary}">
        <div class="email">
          <label for="email">
            <email />
          </label>
          <input type="email" :class="[emailUser ? 'input_active' : '']" v-model="emailUser" placeholder="Masukan Email Anda" id="email"/>
        </div>
           <div class="text-danger">{{ message.email ?? '' }}</div>
       <div class="password mt-2" >
          <label for="password">
            <key />
          </label>
          <input type="password" :class="[passUser ? 'input_active' : '']" v-model="passUser" placeholder="Masukan password" id="password">
        </div>
           <div class="text-danger">{{ message.password ?? '' }}</div>
       <button class="btn_log">Masuk</button>
        <div class="opsi">
           <router-link to="/forgot" class="forget">
            Lupa Sandi
          </router-link>    
        </div>
      </form> 
          <router-link to="/register">
            <button class="btn_log new_acc">Buat Akun Baru</button>
          </router-link>  
    </div>
  </div>
  <Footer />
</template>

<script>

import email from '../../icon/email'
import key from '../../icon/key'
import Footer from '../../components/Footer'
import gsap from 'gsap'

  export default{
    name: 'lin',
    methods: {
     async onSubmit(){
       try {
         const data = await this.axios.post('/LoggedIn', {
           email: this.emailUser,
           password: this.passUser
         });

         const response = data.data.response;
         //i this.$router.push('/home');
         if(data.status === 200) {
           //const token = atob(decodeURIComponent(response.token));
           if(response.status) {
            this.message = response.message;
            localStorage.setItem('email', this.emailUser);
             localStorage.setItem('xtoken', response.token)
           const s = localStorage.getItem('email');
             if(s) {
               this.$router.push('/home')
             }
           }
         }
       } catch(e) {
         alert(e)
       }
      }
    },
    data() {
      return {
        emailUser: '',
        passUser: '',
        message: {}
      }
    },
    props: {
      color: Object
    },
    components: {
      email,
      key,
      Footer
    },
    mounted() {
      const g = gsap.timeline();
      g.from(this.$refs.title, {
        duration: 0.2,
        opacity: 0,
        y: 50
      }).from(this.$refs.desc, {
        duration: 0.2,
        opacity: 0,
        y: 50
      }).from(this.$refs.form, {
        duration: 0.2,
        opacity: 0,
        y: 50
      })
    }
  }
</script>

