<template>
  <div class="bagian_body">
    <div class="container">
      <p class="text_body" v-if="text.length > 70" @click="bukaText">
        {{ tsil }}
      </p>
      <p v-else>
        {{ text }}
      </p>
      <carousel class="img_body" v-if="img">
        <slide href="#" class="gmbr" v-for="(id, i) in img" :key="i">
          <img :src="id" alt="Image">
        </slide>

        <template #addons>
          <pagination />
        </template>
      </carousel>
    </div>
  </div>
</template>


<script>
import 'vue3-carousel/dist/carousel.css';
import { Carousel, Slide, Pagination} from 'vue3-carousel';

  export default{
    name: 'bagianBody',
    components: {
      Carousel,
      Slide,
      Pagination,
    },
    data() {
      return {
        tsil: ''
      }
    },
    methods: {
      bukaText() {
        this.tsil = this.text;
      }
    },
    props: {
      color: Object,
      text: String,
      img: {
        type: Array,
      }
    },
    created(){
       this.tsil = this.text.slice(0, 70) + "..."
    }
  }
</script>

