<template>
  <div class="fixed_top">
<Header :cari="srch" :warna="color" @search="searching" v-show="tampilNav"/>
  </div>
  <div class="home top-3">
      <div class="story" v-for="(data, i) in results" :key="i">
       <storyCard :warna="color" :data="data"/>
      </div>
  </div>
</template>

<script>
import storyCard from '../components/mid/storyCard.vue'
import Header from '../components/Header.vue'


export default {
  name: 'Home',
  data() {
    return{
      color: {
        primary: '#0091F6',
        hearth: '#EF5B5B',
        text: '#929292',
        subtext: '#CAC8C8',
        retangle: '#F8F8F8',
        black: '#000'
      },
      results: [
      ],
      tampilNav: true,
      srch : '',
      token: localStorage.getItem('xtoken')
    }
  },
  components: {
    storyCard,
    Header
  },
  props: {
    nama: String
  },
  methods: {
    searching(text) {
      this.text = text;
    },
    async rundata() {
      try {
        const results = await this.axios.get('/Postdata', {
          headers: {
            authorization: `Bearer ${this.token}`
          }
        });
        this.results = results.data;
      } catch(err) {
        alert(err)
      }
    }
  },
  created() {
    this.$emit('hilang-navbar', true);
    this.rundata();
  }
}
</script>

