<template>
  <div class="wraper top-3" :style="{'--bg': '#fff'}">
    <div class="container_c">
      <div class="kolom">
        <Search :warna="warna" :input="inp" @search="cariin"/>
      </div>
      <div class="kolom chat" v-if="users.length > 0">
        <cardChat v-for="user in users" :key="user.id" :warna="warna" :user="user" @hilang-navbar="$emit('hilang-navbar', false)"/>
      </div>
      <div class="no-chat" v-else>
        <p class="text-center nc">Heyoo! untuk saat ini Kamu belum berteman dengan siapapun, <router-link to="/friends" :style="{color: warna.primary}">Yuk cari teman!</router-link></p>
      </div>
    </div>
  </div>
</template>

<script>
import Search from '../components/mid/Search'
import cardChat from '../components/etc/cardChat.vue'

  export default {
    name: 'Chat',
    data(){
      return {
        inp: true,
        users: [
          
        ],
        gatherData: {},
        token: localStorage.getItem('xtoken') ?? this.$route.push("/login")
       }
    },
    props: {
      warna: Object
    },
    components: {
      Search,
      cardChat
    },
    methods: {
      cariin(text) {
        if(text != '') {
         this.users = this.users.filter(user => user.name.includes(text) );
        } 
      },
      async getData() {
        try {
          const data = await this.axios.get('/Friends/me', {
            headers: {
              Authorization: `Bearer ${this.token}`
            }
          })
          console.log(data);
        } catch(e) {
          console.log(e)
        }
      }
    },
    mounted() {
      this.$emit('hilang-navbar', true)
      this.getData()
    }
  }
</script>

