<template>
  <div class="wraper">
   <div class="container">
    <div class="cput">
      <input v-model="chat" placeholder="Type a message..." :style="{'--subt': warna.subtext}">
    </div>
    <button class="send" @click="handleSend">
<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M24 0l-6 22-8.129-7.239 7.802-8.234-10.458 7.227-7.215-1.754 24-12zm-15 16.668v7.332l3.258-4.431-3.258-2.901z"/></svg>
    </button>
  </div>
</div>
</template>

<script>
  export default {
    name: 'inputChat',
    data(){
      return {
        chat: '',
      }
    },
    methods: {
      handleSend() {
        let data = {
          text: this.chat,
          isMe: true
        }
        this.$emit('send-pesan', data);
        this.chat = '';
      },
    },
    props: {
      warna: {
        type: Object,
        required: true
      }
    },
  }
</script>

<style scoped>

.wraper {
    max-width: 800px;
    margin: 0 auto;
     bottom: 0;
    left: 0;
    right: 0;
    z-index: 9;
   position: fixed;
}
  .container {
    display: flex;
    margin: 1rem;
    justify-content: center;
    align-items: center;
  }

  .cput input {
    border: none;
    background: var(--subt);
    width: 90%;
    padding: 0.4rem;
    outline: none;
    border-radius: 0.4rem;
  }

  .send {
    border: none;
    background: transparent;
  }
</style>
