<template>
  <div class="wraper_log" :style="{'--primary': color.primary, '--red': color.hearth}">
    <div class="container_log">
      <div class="header_log">
        <h1 :style="{color: color.text}"><span :style="{color: color.primary}">HeYoo</span> Story</h1>
        <p :style="{lineHeight: '140%', color: color.text, letterSpacing: '0.4px' }">Register ke <span :style="{color: color.primary}">HeYoo</span> untuk <b>berinteraksi</b> dengan teman baru, sekarang waktunya untuk memulai berbagi <b>pengetahuan</b> dan <b>cerita</b> dengan teman mu</p>
      </div>
      <form class="body_log" @submit.prevent="onSubmit" :style="{'--primary': color.primary}">
        <div class="username d-flex">
          <label for="username">
            <user w="30px"/>
          </label>
          <input type="text" class="form-input" v-model="nameUser" placeholder="Masuka nama Anda" id="username"/>
        </div>      
         <p class="text-danger">{{ message.nama ? message.nama : '' }}</p>
      <div class="email mt-2">
          <label for="email">
            <email />
          </label>
          <input type="email" class="form-input" v-model="emailUser" placeholder="Masukan Email Anda" id="email"/>
        </div>
         <p class="text-danger">{{ message.email ? message.email : '' }}</p>
       <div class="password mt-2" >
          <label for="password">
            <lock w="30px" h="30px"/>
          </label>
          <input type="password" class="form-input" v-model="passUser" placeholder="Masukan password" id="password">
        </div>
        <div class="password mt-2" >
          <label for="password2">
            <key />
          </label>
          <input type="password" class="form-input" v-model="passUserConfirm" placeholder="Konfirmasi password" id="password2">
        </div>
        <p class="text-danger">{{ message.password ? message.password : '' }}</p>

        <button class="btn_log">Daftar</button>
        <div class="opsi">
           <router-link to="/forgot" class="forget">
            Lupa Sandi
          </router-link> 
        </div>
      </form>
      <router-link to="/login">
        <button class="btn_log new_acc">Login</button>
      </router-link>
    </div>
  </div>
  <Footer />
</template>

<script>

import email from '../../icon/email'
import key from '../../icon/key'
import Footer from '../../components/Footer'
import user from '../../icon/user.vue'
import lock from '../../icon/lock.vue'

  export default{
    name: 'lin',
    data() {
      return {
        nameUser: '',
        emailUser: '',
        passUser: '',
        passUserConfirm: '',
        message: {}
      }
    },
    props: {
      color: Object
    },
    components: {
      email,
      key,
      Footer,
      user,
      lock
    },
    methods: {
     async onSubmit() {
        const data = {
          fullname: this.nameUser,
          email: this.emailUser,
          password: (this.passUser == this.passUserConfirm) ? this.passUser : this.$swal({
            icon: 'error',
            title: 'Password konfirmasi salah!',
            text: 'Pastikan kode konfirmasi telah benar'
          })
        }
       try {
        const sendData = await this.axios.post('/Authentification', data)
         const isi = sendData.data
         let swal = {
           icon: '',
           title: '',
           text: ''
         }
         const pesan = isi.response.message
         if(isi.response.status) {
           swal = {
             icon: 'success',
             title: 'Register berhasil',
             text: pesan
           }
           this.$swal(swal)
         } else {
           this.message = {
             nama: pesan.fullname ? pesan.fullname : '',
             email: pesan.email ? pesan.email : '',
             password: pesan.password ? pesan.password : ''
           }
         }
       } catch(e) {
         alert(e)
       }
     }
    }
  }
</script>

